.home {
  &__wrapper {
    display: flex;
    justify-content: space-between;
    min-height: 100vh;
    max-height: 100vh;
    align-items: center;
  }
  &__info {
    max-width: 684px;
  }
  span {
    color: #FFFF33;
    font-weight: 500;
    font-size: 24px;
    margin-bottom: 15px;
    display: inline-block;
    + span {
      margin-left: 25px;
    }
  }
  h1 {
    font-weight: 600;
    font-size: 64px;
    color: #fff;
    margin-bottom: 25px;
  }
  p {
    color: #FFFF33;
    width: fit-content;
    margin-bottom: 50px;
    padding-right: 34px;
    position: relative;
    &::after {
      content: url("../images/yellow-right.svg");
      position: absolute;
      top: 50%;
      width: 24px;
      height: 24px;
      transform: translateY(-50%);
      right: 0;
    }
  }
}

.title {
  padding-bottom: 15px;
  border-bottom: 1px solid #D9DAD9;

  h2,span {
    font-weight: 600;
    font-size: 40px;
    text-transform: uppercase;
    color: #FFFFFF;
  }

  h2 {
    padding-left: 378px;
    position: relative;
  }

  span {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
  }
}

.about {
  margin-bottom: 300px;
  &__info {
    padding-left: 378px;
    margin: 50px 0 150px 0;
    p:not(:last-child) {
      margin-bottom: 15px;
    }
  }
}

.offer {
  margin-bottom: 300px;
  &__list {
    display: flex;
    align-items: center;
    justify-content: space-between;
    max-width: 1200px;
    margin: 150px auto 0 auto;
    li {
      position: relative;
      width: fit-content;
      height: 100%;
      p {
        font-weight: 600;
        font-size: 36px;
        text-transform: uppercase;
        color: #FFFFFF;
        width: fit-content;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
      }
    }
  }
}

.mission {
  margin-bottom: 300px;
  &__list {
    margin-top: 50px;
    li {
      display: flex;
      img{
        max-width: 346px;
        width: 100%;
        object-fit: cover;
        margin-right: 33px;
      }
      h4 {
        font-size: 24px;
        font-weight: 500;
        color: #FFFF33;
        margin-bottom: 10px;
      }
      &:not(:last-child) {
        margin-bottom: 100px;
      }
    }
  }
}

.contact {
  margin-bottom: 150px;
  &__info {
    padding-left: 378px;
    display: flex;
    margin: 50px 0;
    li {
      +li {
        margin-left: 32px;
      }
      h4 {
        font-weight: 500;
        font-size: 24px;
        margin-bottom: 10px;
        color: #FFFF33;
      }
      a {
        color: #FFFFFF;
      }
      &:nth-child(3){
        a {
          display: inline-block;
          &:last-child{
            margin-left: 25px;
          }
        }
      }
    }
  }
}