.header {
  background-color: #061012;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}

.menu {
  height: 80px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  &__list {
    display: flex;
    margin: 0 auto 0 22%;
    li {
      + li {
        margin-left: 43px;
      }
    }
    a {
      font-weight: 500;
      font-size: 18px;
      line-height: 24px;
    }
  }
  &__social {
    display: flex;
    li {
      + li {
        margin-left: 25px;
      }
    }
  }
  &__copy {
    display: flex;
    font-weight: 500;
    font-size: 18px;
    line-height: 24px;
    li:first-child {
      margin-right: 90px;
    }
    li:last-child {
      margin-left: 25px;
    }
  }
}