body {
  cursor: default;
  color: #D9DAD9;
  line-height: 1.2;
  font-size: 20px;
  font-family: "Basically A Sans Serif", sans-serif;
  font-weight: 400;
  background-color: #08171A;
}
.container {
  margin: 0 auto;
  max-width: 1514px;
  padding: 0 17px;
}

.button {
  height: 54px;
  background: #FFFF33;
  border-radius: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 25px;
  font-weight: 600;
  color: #000000;
  width: fit-content;
  img {
    margin-left: 15px;
  }
}